.img-container{
    width: 300px;
    height: 300px;
}
.img-container:hover {
    width: 350px;
    height: 350px;
    transition: width 0.25s, height 0.25s;
  }



.parallax {

  background-image: url("https://images-na.ssl-images-amazon.com/images/I/91I5cPaqB7L._RI_.jpg");

  min-height: 500px; 

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
